<template>
  <div :style="minePageStyle">
    <van-cell title="播放记录" icon="clock-o" size="large" is-link @click="toHistoryPage"/>
      <div class="horizontal-container">
        <div class="scroll-wrapper" ref="scroll">
          <div class="scroll-content" :style="scrollContentStyle">
            <div class="scroll-item" 
              v-for="vod of vods" 
              :key="vod.vodId"
              @click="handleToDetail(vod.vodId)"
              >
              <img v-lazy="vod.cover" :style="coverStyle" />
              <span class="span-name" :style="titleStyle">{{ vod.vodName }}</span>
            </div>
          </div>
        </div>
      </div>
    <van-cell title="我的收藏" icon="like-o" size="large" is-link @click="toLikePage"/>
    <van-cell title="我的下载" icon="down" size="large" is-link v-show="false"/>
    <van-cell title="分享好友" @click="dakaiwx"  icon="share-o" size="large" is-link />
    <van-cell title="打开官网" @click="openUrl"  icon="info-o" size="large" is-link />
  </div>
</template>

<script>
import { Cell, CellGroup, Toast} from 'vant'
import 'vant/lib/cell/style'
import 'vant/lib/cell-group/style'
import 'vant/lib/toast/style'
import db from 'common/db.js'
import BScroll from '@better-scroll/core'

export default {
  name: 'MinePage',
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Toast.name]: Toast,
  },
  mounted () {
  },
  activated() {
    this.queryHistory()
    this.initScroll()
  },
  beforeDestroy() {
    this.bs.destroy()
  },
  data () {
    return {
      vods:[],
    }
  },
  watch: {
    scrollContentStyle(newValue){
      this.$nextTick(() => {
        if (this.bs) {
          this.bs.refresh()
        }
      })
    }
  },
  computed : {
    minePageStyle () {
      return {
        width: `${this.$utils.screenWidth / 50}rem`,
        height: `${this.$utils.screenHeight / 50}rem`,
        marginTop: '.2rem',
        position:'relative', 
        background: '#fff',
        overflow :'hidden',
      }
    },
    scrollContentStyle () {
      console.log(`${this.vods.length}`)
      let widthtep = 2.34 
      let heighttep = 3.11 
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth + 0.16
        heighttep = this.$utils.vodlistHeight
      }
      if (this.vods.length <= 0){
        heighttep = 0
      }
      const width = `${(widthtep * this.vods.length).toFixed(2)}rem`
      console.log(`${width}`)
      return {
        width: width,
        height: heighttep + 'rem',
        display: 'inline-block'
      }
    },
    coverStyle() {
      let widthtep = 2.34 
      let heighttep = 3.11 
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth
        heighttep = this.$utils.vodlistHeight
      }
      return {
        width: `${widthtep}rem`,
        height: `${heighttep}rem`,
        display: 'block',
        objectFit: 'cover',
      }
    },
    titleStyle () {
      let widthtep = 2.34 
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth
      }
      console.log(`${widthtep}`)
      return {
        width: widthtep + 'rem',
        display: 'inline-block',
        justifyContent: 'center',
        alignItems: 'center',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        whiteSpace: 'nowrap',
        fontSize: '.26rem',
        marginTop: '.06rem',
        textAlign: 'center',
      }
    } 
  },
  methods: {
    initScroll () {
      this.bs = new BScroll(this.$refs.scroll, {
        scrollX: true,
        scrollY: false,
        click: true
      })
    },
    queryHistory() {
      this.vods = []
      db.vodhistory.toArray().then((list)=>{
        console.log(list)
        this.vods.push(...list)
      })
    },
    handleToDetail (id) {
      this.$router.push({ path: `/detail/${id}` })
    },
    toHistoryPage () {
      this.$router.push({ path: `/history` })
    },
    toLikePage () {
      this.$router.push({ path: `/like` })
    },
    openUrl() {
      if (this.$utils.config.gw){
        window.location.href = 'jdzj://function?opengw='+this.$utils.config.gw
      }
    },
    dakaiwx() {
      if (this.$utils.config.shareText){
        window.location.href = 'jdzj://function?dakaiwx='+this.$utils.config.shareText
        Toast('复制成功，请打开微信粘贴')
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .horizontal-container
    .scroll-wrapper
      position relative
      width 100%
      white-space nowrap
      overflow hidden
      margin-left: .3rem
      .scroll-content
        display inline-block
      .scroll-item
        height 100%
        line-height 100%
        display inline-block
        margin-right: .1rem
            
</style>